
const a = {"":"YO","":"I","":"TS","":"U","":"K","":"E","":"N","":"G","":"SH","":"SCH","":"Z","":"H","":"-","":"yo","":"i","":"ts","":"u","":"k","":"e","":"n","":"g","":"sh","":"sch","":"z","":"h","":"-","":"F","":"I","":"V","":"a","":"P","":"R","":"O","":"L","":"D","":"ZH","":"E","":"f","":"i","":"v","":"a","":"p","":"r","":"o","":"l","":"d","":"zh","":"e","":"Ya","":"CH","":"S","":"M","":"I","":"T","":"-","":"B","":"YU","":"ya","":"ch","":"s","":"m","":"i","":"t","":"-","":"b","":"yu"};

export function transliterate(word){
  return word.split('').map(function (char) { 
    return a[char] || char; 
  }).join("");
}

